            if(this.password==this.password2){
                 axios
        .post("http://localhost:8080/users/register", {
          
            phone: this.phone,
            password: this.password,
            money:"0",
            coupon:"0",
          })
        .then(
          (response) => {
            if (response.data.status == 1) {
              console.log("");
              this.$router.push('/login');
            } else {
              alert("");
            }
          },
          (error) => {
            console.log("vue", error.message);
          }
        );
            }
               else{
                alert("")
               }     
          }else{
            alert("")
          }
  
    },
  }
};
